.header {
  background-color: rgb(237, 63, 63);
  width: 100vw;
  height: 100px;
  box-shadow: 0px 9px 14px 1px rgba(0, 0, 0, 0.2);
  display: flex;
}

.headerTitle {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.settingsLogo {
  height: 35px;
}

.settingsButton {
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
}

.headerSettings {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100px;
  justify-content: flex-end;
  align-items: center;
  right: 20px;
}

.storeContainer {
  min-width: calc(100vw - 40px);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 20px 20px 20px 20px;
}

.categoryHeader {
  height: 60px;
  background-color: #f3f3f3;
  color: black;
  font-size: 22px;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.separator {
  width: 100%;
  height: 1px;
  padding-bottom: 1px;
  border-bottom: 2px dashed #dedede;
  background-color: #f3f3f3;
}

.categoryCards {
  display: flex;
  flex-wrap: wrap;
  align-self: flex-start;
}

.hidden {
  display: none;
}

.categoryIsOpen {
  transform: rotate(180deg);
}

.arrowIcon {
  margin-left: 25px;
  height: 22px;
}

.categoryTitle {
  margin-left: 10px;
}

.itemsContainer {
  width: 70%;
  min-width: 410px;
  display: block;
  margin-top: 20px;
}

.cartContainer {
  width: 30%;
  display: flex;
  justify-content: flex-end;
}

.price {
  font-size: 16px;
  color: #dc3e28;
  font-family: 'Raleway', sans-serif;
}

.name {
  font-size: 16px;
  font-weight: bold;
  font-family: 'Raleway', sans-serif;
}

p {
  margin: 0;
}

.logo {
  height: 55px;
}

.title {
  color: white;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  font-size: 30px;
  margin-left: 20px;
}

.footer {
  display: flex;
  justify-content: center;
  margin-top: 90px;
  margin-bottom: 20px;
}

.footerText {
  font-family: 'Raleway', sans-serif;
  color: #adadad;
}

.modal {
  background-color: white;
  width: 30%;
  min-width: 400px;
  height: 80vh;
  min-height: 500px;
  margin: 10vh auto 0 auto;
  outline: none;
  border-radius: 3px;
}

.modalHeader {
  background-color: #f3f3f3;
  width: 100%;
  height: 100px;
  border-radius: 3px 3px;
  display: flex;
}

.modalTitle {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.exitContainer {
  width: 30%;
  min-width: 400px;
  height: 100px;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.exitButton {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0;
  width: 50px;
  height: 50px;
}

.exitLogo {
  height: 50px;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

.preferences {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.helpMessage {
  display: flex;
  justify-content: center;
  color: black;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  font-size: 14px;
}

.modalSubHeader {
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  color: black;
  font-size: 26px;
}

.prefContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  margin: 30px 10px 20px 10px;
}

.radioButton {
  margin: 0 10px 20px 10px;
  background-color: white;
  border: 3px solid black;
  border-radius: 3px;
  min-width: 110px;
  height: 50px;
  color: #737373;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  font-size: 16px;
  outline: none;
  cursor: pointer;
  transition: all 0.2s;
}

.focused {
  background-color: #07b0ed;
  border-color: #07b0ed;
  color: white;
}
.mylogo{
  height: 100px;
  width: 100px;
}
.settingsButton {
  background: none;
  border: none;
  cursor: pointer;
}

.settingsLogo {
  width: 24px; /* Adjust the width as needed */
  height: 24px; /* Adjust the height as needed */
}
.settingsmylogo {
  width: 24px; /* Adjust the width as needed */
  height: 24px; /* Adjust the height as needed */
}
.frenchButton {
  background-color: rgb(237, 63, 63);
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  position: absolute;
  top: 45%;
  left: 30px;
  transform: translateY(-50%);
}
.customLink {
  color: white;
  text-decoration: none;
}

.modal {
  background-color: white;
  width: 30%;
  min-width: 400px;
  height: 80vh;
  min-height: 500px;
  margin: 10vh auto 0 auto;
  outline: none;
  border-radius: 3px;
  pointer-events: auto; /* Add this line */
}
.footer-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.footer-container a {
  margin: 0 10px;
}

.footer-container img {
  width: 40px;
  height: 40px;
}
